import React, { useState } from "react";
import {
    Carousel,
    CarouselItem,
    CarouselControl,
    CarouselIndicators,
} from "reactstrap";
import data from "../data.json";
import Articles from "../Articles/index";

const Example = (props) => {
    const [activeIndex, setActiveIndex] = useState(0);
    const [animating, setAnimating] = useState(false);
    const { employeeStories } = data;

    const next = () => {
        if (animating) {
            return;
        }
        const nextIndex =
            activeIndex === employeeStories.data.length - 1
                ? 0
                : activeIndex + 1;
        setActiveIndex(nextIndex);
    };

    const previous = () => {
        if (animating) {
            return;
        }
        const nextIndex =
            activeIndex === 0
                ? employeeStories.data.length - 1
                : activeIndex - 1;
        setActiveIndex(nextIndex);
    };

    const goToIndex = (newIndex) => {
        if (animating) {
            return;
        }
        setActiveIndex(newIndex);
    };

    const slides = employeeStories.data.map((data) => (
        <CarouselItem
            onExiting={() => setAnimating(true)}
            onExited={() => setAnimating(false)}
            key={data.employeeImage}
        >
            <div className="col-lg-3 col-md-3 carousel-caption  carousel-head">
                {/* <h1 className="light-green-bg font-xl-l maison-extrabold py-xl-5 py-md-3  text-white">Employee Stories</h1> */}
            </div>
            <img
                className="d-block w-100"
                src={data.employeeImage}
                alt={data.employeeName}
            />
            <div className="emp-testimonial carousel-caption text-left p-5 font-xs-l">
                <p className="text-white emp-testimonial-text roboto-regular">
                    {` `}
                    {data.testimonial}
                    {` `}
                </p>
                <h5 className="text-green maison-bold">{data.employeeName}</h5>
            </div>
        </CarouselItem>
    ));

    // Mobile slides

    const mobileSlides = employeeStories.data.map((data) => (
        <CarouselItem
            onExiting={() => setAnimating(true)}
            onExited={() => setAnimating(false)}
            key={data.employeeImage}
        >
            <div className="card bg-white text-center">
                <div className="card-img-top light-green-bg font-xl-l maison-bold text-white py-2">
                    Employee Stories
                </div>
                <img
                    className="card-img-top"
                    src={data.employeeImage}
                    alt={data.employeeName}
                />
                <div className="card-body px-3 font-xs-l">
                    <p className="text-dark">{data.testimonial}</p>
                    <h5 className="text-green maison-bold">
                        {data.employeeName}
                        {` `}
                    </h5>
                </div>
            </div>
        </CarouselItem>
    ));

    return (
        <section className="gray-bg py-5">
            <div className="container employee-stories">
                {/* Employee stories desktop view */}
                <p className="d-none d-md-block font-xl-x text-center text-green neosans-regular pt-5 pb-2 ">
                    Employee Stories
                </p>
                <div className="row d-none d-md-block">
                    <div
                        className="col-md-12 px-0 main-carousel"
                        style={{ boxShadow: `-25px 25px #009944` }}
                    >
                        <Carousel
                            id="impactSlider"
                            activeIndex={activeIndex}
                            next={next}
                            previous={previous}
                            className="carousel-fade"
                        >
                            <CarouselIndicators
                                items={employeeStories.data}
                                activeIndex={activeIndex}
                                onClickHandler={goToIndex}
                                id="impactSlider"
                                className="careers-carousel-indicators  justify-content-end"
                            />
                            {slides}
                        </Carousel>
                    </div>
                </div>
                <div className="d-block d-md-none">
                    <Carousel
                        id="impactSlider"
                        activeIndex={activeIndex}
                        next={next}
                        previous={previous}
                        className="carousel-fade"
                    >
                        <CarouselIndicators
                            items={employeeStories.data}
                            activeIndex={activeIndex}
                            onClickHandler={goToIndex}
                            id="impactSlider"
                            className="careers-carousel-indicators  justify-content-end"
                        />
                        {mobileSlides}
                        <div id="carouselExampleControls">
                            <CarouselControl
                                direction="prev"
                                directionText="Previous"
                                onClickHandler={previous}
                            />
                            <CarouselControl
                                direction="next"
                                directionText="Next"
                                onClickHandler={next}
                            />
                        </div>
                    </Carousel>
                </div>
                <div className="row">
                    <a
                        href="/all-open-positions/"
                        className="btn-success btn text-uppercase mt-5 col-md-3 col-6 text-center mx-auto"
                    >
                        {` `}
                        View all positions
                    </a>
                </div>
            </div>
            {/* GOJEK articles */}
            <Articles />
        </section>
    );
};

export default Example;
