import React, { Component } from "react";
import { Link, navigate } from "@reach/router";
import {
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
} from "reactstrap";
import { getSlug } from "../../Common/utils/getSlug.js";

export const getReformattedPlaces = (places) => {
    const placesRestricted = [
        "Pekanbaru",
        "Manado",
        "Palembang",
        "Tangerang",
        "Bandung",
        "Batam",
        "North Sumatra",
        "EJBN",
        "East Java and Bali",
        "Kalimantan",
        "Sumatra",
        "East Indonesia",
        "Jakarta/Singapore/Bengaluru",
        "Bogor",
        "Medan",
        "Surabaya",
        "Semarang",
        "Pangandaran",
    ];
    let returnedData = [];
    places.map((place) => {
        if (!placesRestricted.includes(place)) {
            returnedData.push(place);
        }
    });

    return returnedData;
};

